import React from "react";
import styled from "styled-components";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/effect-cards";
import "swiper/css/pagination";
import "swiper/css/navigation";


import { EffectCards, Pagination, Navigation, Autoplay } from "swiper";

import img1 from '../assets/Monkey/0001.png';
import img2 from '../assets/Monkey/0002.png';
import img3 from '../assets/Monkey/0003.png';
import img4 from '../assets/Monkey/0004.png';
import img5 from '../assets/Monkey/0005.png';
import img6 from '../assets/Monkey/0006.png';
import img7 from '../assets/Monkey/0007.png';
import img8 from '../assets/Monkey/0008.png';
import img9 from '../assets/Monkey/0009.png';
import img10 from '../assets/Monkey/0010.png'

import Arrow from '../assets/Arrow.svg'


const Container = styled.div`
width: 500px;
height: 500px;

@media (max-width: 700px){
    hight: 300px;
    width: 300px;
}

.swiper{
    width: 100%;
    height: 100%;
}
.swiper-slide{
    border-radius: 20px;

    display: flex;
    justify-content: center;
    align-items: center;

}
.swiper-button-next{
    color: white;
    right: 0;
    width: 4rem;
    top: 60%;

    background-image: url(${Arrow});
    background-position: center;
    background-size: cover;

    &:after{
        display: none;
    }
    @media (max-width: 700px){
        width: 3rem;
    }
    @media (max-width: 400px){
        width: 2rem;
    }
}
.swiper-button-prev{
    color: white;
    left: 0;
    width: 4rem;
    transform: rotate(180deg);
    top: 60%;

    background-image: url(${Arrow});
    background-position: center;
    background-size: cover;

    &:after{
        display: none;
    }
    @media (max-width: 700px){
        width: 3rem;
    }
    @media (max-width: 400px){
        width: 2rem;
    }
}

img{
    max-width: 500px;
    max-height: 500px;
}
`

const Carousel = () => {
    return (
        <Container>
            <Swiper
            autoplay={{
                delay:2000,
                disableOnInteraction: false,
            }}
            pagination={{
                type: 'fraction',
            }}
            navigation={true}
            scrollbar={{
                draggable: true
            }}
        effect={"cards"}
        grabCursor={true}
        modules={[EffectCards, Pagination, Navigation, Autoplay ]}
        className="mySwiper"
      >
        <SwiperSlide>  <img src={img1} alt="MonkeyVerse" />  </SwiperSlide>
        <SwiperSlide>  <img src={img2} alt="MonkeyVerse" />  </SwiperSlide>
        <SwiperSlide>  <img src={img3} alt="MonkeyVerse" />  </SwiperSlide>
        <SwiperSlide>  <img src={img4} alt="MonkeyVerse" />  </SwiperSlide>
        <SwiperSlide>  <img src={img5} alt="MonkeyVerse" />  </SwiperSlide>
        <SwiperSlide>  <img src={img6} alt="MonkeyVerse" />  </SwiperSlide>
        <SwiperSlide>  <img src={img7} alt="MonkeyVerse" />  </SwiperSlide>
        <SwiperSlide>  <img src={img8} alt="MonkeyVerse" />  </SwiperSlide>
        <SwiperSlide>  <img src={img9} alt="MonkeyVerse" />  </SwiperSlide>
        <SwiperSlide>  <img src={img10} alt="MonkeyVerse" />  </SwiperSlide>
      </Swiper>
        </Container>
    )
}

export default Carousel